* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: "none";
  -moz-user-select: none;
  -webkit-user-select: none;
  outline: none !important;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  text-decoration: none;
}

.floor,
#blocks path {
  fill: transparent;
  fill-opacity: 0;
  cursor: pointer;
}

#blocks path:hover {
  fill-opacity: 0.2;
  fill: rgb(0, 255, 0);
  stroke: rgba(0, 0, 0, 0.616);
  stroke-width: 2px;
}

.floor:hover,
.flat:hover {
  fill-opacity: 0.3;
  fill: rgb(0, 255, 0);
  stroke: rgba(0, 0, 0, 0.534);
  stroke-width: 1px;
}

path:focus {
  outline: none;
  user-select: none;
}

body {
  position: relative;
  height: 50%;
}

.tippy-box {
  pointer-events: all !important;
}

#tippy-7 {
  z-index: 1;
  pointer-events: all !important;
}

.tippy-box:hover {
  display: none;
}
